html, body, #root {
  height: 100%;
  width: 100%;
}

.TicTacToe {
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-top: 10px;
}

.Grid {
  margin: 0 auto;
  border-collapse: collapse;
  border-spacing: 0;
}

.Grid-true {
  background: #eee;
}

td {
  border: 1px solid black;
  width: 150px;
  height: 150px;
  text-align: center;
}

button {
  cursor: pointer;
  display: block;
  background: #ddd;
  height: 40px;
  width: 60px;
  font-size: 14px;
  border: none;
  margin: 40px auto 0;
}

button:hover {
  border: 2px solid #ccc;
}

@media (max-width: 500px) {
  td {
    width: 100px;
    height: 100px;
  }
}



